import {Component, Input, Output, EventEmitter, OnChanges, ChangeDetectionStrategy } from '@angular/core';

import { Grid } from '../../../lib/grid';
import { Row } from '../../../lib/data-set/row';
import { DataSource } from '../../../lib/data-source/data-source';

@Component({
  selector: 'ng2-st-tbody-edit-delete',
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <a href="#" *ngIf="isActionEdit" class="ng2-smart-action ng2-smart-action-edit-edit"
        [innerHTML]="editRowButtonContent" (click)="onEdit($event)"></a>
    <a href="#" *ngIf="isActionDelete" class="ng2-smart-action ng2-smart-action-delete-delete"
        [innerHTML]="deleteRowButtonContent" (click)="onDelete($event)"></a>
  <a href="#" *ngIf="isActionCustom1" class="ng2-smart-action ng2-smart-action-custom1-custom1"
        [innerHTML]="custom1RowButtonContent" (click)="onCustom1($event)"></a>
  <a href="#" *ngIf="isActionCustom2" class="ng2-smart-action ng2-smart-action-custom2-custom2"
        [innerHTML]="custom2RowButtonContent" (click)="onCustom2($event)"></a>
  <a href="#" *ngIf="isActionCustom3" class="ng2-smart-action ng2-smart-action-custom3-custom3"
        [innerHTML]="custom3RowButtonContent" (click)="onCustom3($event)"></a>
<a href="#" *ngIf="isActionCustom4" class="ng2-smart-action ng2-smart-action-custom4-custom4"
        [innerHTML]="custom4RowButtonContent" (click)="onCustom4($event)"></a>
  <a href="#" *ngIf="isActionCustom5" class="ng2-smart-action ng2-smart-action-custom5-custom5"
        [innerHTML]="custom5RowButtonContent" (click)="onCustom5($event)"></a>
  <a href="#" *ngIf="isActionCustom6" class="ng2-smart-action ng2-smart-action-custom6-custom6"
        [innerHTML]="custom6RowButtonContent" (click)="onCustom6($event)"></a>
  <a href="#" *ngIf="isActionCustom7" class="ng2-smart-action ng2-smart-action-custom7-custom7"
        [innerHTML]="custom7RowButtonContent" (click)="onCustom7($event)"></a>
  `,
})
export class TbodyEditDeleteComponent implements OnChanges {

  @Input() grid: Grid;
  @Input() row: Row;
  @Input() source: DataSource;
  @Input() deleteConfirm: EventEmitter<any>;
  @Input() editConfirm: EventEmitter<any>;

  @Output() edit = new EventEmitter<any>();
  @Output() delete = new EventEmitter<any>();
  @Output() custom1 = new EventEmitter<any>();
  @Output() custom2 = new EventEmitter<any>();
  @Output() custom3 = new EventEmitter<any>();
  @Output() custom4 = new EventEmitter<any>();
  @Output() custom5 = new EventEmitter<any>();
  @Output() custom6 = new EventEmitter<any>();
  @Output() custom7 = new EventEmitter<any>();
  @Output() editRowSelect = new EventEmitter<any>();

  isActionEdit: boolean;
  isActionDelete: boolean;
  isActionCustom1: boolean;
  isActionCustom2: boolean;
  isActionCustom3: boolean;
  isActionCustom4: boolean;
  isActionCustom5: boolean;
  isActionCustom6: boolean;
  isActionCustom7: boolean;
  editRowButtonContent: string;
  deleteRowButtonContent: string;
  custom1RowButtonContent: string;
  custom2RowButtonContent: string;
  custom3RowButtonContent: string;
  custom4RowButtonContent: string;
  custom5RowButtonContent: string;
  custom6RowButtonContent: string;
  custom7RowButtonContent: string;

  onEdit(event: any) {
    event.preventDefault();
    event.stopPropagation();

    this.editRowSelect.emit(this.row);

    if (this.grid.getSetting('mode') === 'external') {
      this.edit.emit({
        data: this.row.getData(),
        source: this.source,
      });
    } else {
      this.grid.edit(this.row);
    }
  }

  onCustom1(event: any) {
    event.preventDefault();
    event.stopPropagation();

    this.custom1RowSelect.emit(this.row);

    if (this.grid.getSetting('mode') === 'external') {
      this.custom1.emit({
        data: this.row.getData(),
        source: this.source,
      });
    } else {
      this.grid.custom1(this.row);
    }
  }

  onCustom2(event: any) {
    event.preventDefault();
    event.stopPropagation();

    this.custom2RowSelect.emit(this.row);

    if (this.grid.getSetting('mode') === 'external') {
      this.custom2.emit({
        data: this.row.getData(),
        source: this.source,
      });
    } else {
      this.grid.custom2(this.row);
    }
  }

   onCustom3(event: any) {
    event.preventDefault();
    event.stopPropagation();

    this.custom3RowSelect.emit(this.row);

    if (this.grid.getSetting('mode') === 'external') {
      this.custom3.emit({
        data: this.row.getData(),
        source: this.source,
      });
    } else {
      this.grid.custom3(this.row);
    }
  }

   onCustom4(event: any) {
    event.preventDefault();
    event.stopPropagation();

    this.custom4RowSelect.emit(this.row);

    if (this.grid.getSetting('mode') === 'external') {
      this.custom4.emit({
        data: this.row.getData(),
        source: this.source,
      });
    } else {
      this.grid.custom4(this.row);
    
    }
  }

   onCustom5(event: any) {
    event.preventDefault();
    event.stopPropagation();

    this.custom5RowSelect.emit(this.row);

    if (this.grid.getSetting('mode') === 'external') {
      this.custom5.emit({
        data: this.row.getData(),
        source: this.source,
      });
    } else {
      this.grid.custom5(this.row);
    }
  }

   onCustom6(event: any) {
    event.preventDefault();
    event.stopPropagation();

    this.custom6RowSelect.emit(this.row);

    if (this.grid.getSetting('mode') === 'external') {
      this.custom6.emit({
        data: this.row.getData(),
        source: this.source,
      });
    } else {
      this.grid.custom6(this.row);
    }
  }

   onCustom7(event: any) {
    event.preventDefault();
    event.stopPropagation();

    this.custom7RowSelect.emit(this.row);

    if (this.grid.getSetting('mode') === 'external') {
      this.custom7.emit({
        data: this.row.getData(),
        source: this.source,
      });
    } else {
      this.grid.custom7(this.row);

    
    }
  }

  onDelete(event: any) {
    event.preventDefault();
    event.stopPropagation();

    if (this.grid.getSetting('mode') === 'external') {
      this.delete.emit({
        data: this.row.getData(),
        source: this.source,
      });
    } else {
      this.grid.delete(this.row, this.deleteConfirm);
    }
  }

  ngOnChanges(){
    this.isActionEdit = this.grid.getSetting('actions.edit');
    this.isActionDelete = this.grid.getSetting('actions.delete');
        this.isActionCustom1 = this.grid.getSetting('actions.custom1');
            this.isActionCustom2 = this.grid.getSetting('actions.custom2');
            this.isActionCustom3 = this.grid.getSetting('actions.custom3');
            this.isActionCustom4 = this.grid.getSetting('actions.custom4');
            this.isActionCustom5 = this.grid.getSetting('actions.custom5');
            this.isActionCustom6 = this.grid.getSetting('actions.custom6');
            this.isActionCustom7 = this.grid.getSetting('actions.custom7');
    this.editRowButtonContent = this.grid.getSetting('edit.editButtonContent');
    this.deleteRowButtonContent = this.grid.getSetting('delete.deleteButtonContent');
    this.custom1RowButtonContent = this.grid.getSetting('custom1.custom1ButtonContent');
    this.custom2RowButtonContent = this.grid.getSetting('custom2.custom2ButtonContent');
    this.custom3RowButtonContent = this.grid.getSetting('custom3.custom3ButtonContent');
    this.custom4RowButtonContent = this.grid.getSetting('custom4.custom4ButtonContent');
    this.custom5RowButtonContent = this.grid.getSetting('custom5.custom5ButtonContent');
    this.custom6RowButtonContent = this.grid.getSetting('custom6.custom6ButtonContent');
    this.custom7RowButtonContent = this.grid.getSetting('custom7.custom7ButtonContent');
  }
}
